import { Component, AfterViewInit, ElementRef, ViewChild, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { AlertController, LoadingController, NavController, ToastController, Platform  } from '@ionic/angular';
import { AccessProviders } from '../providers/access-providers';
import { Storage } from '@ionic/storage';
import { DataService } from '../data.service';
import { NavparamService } from '../navparam.service';
import { Button } from 'protractor';

@Component({
  selector: 'app-edit-account',
  templateUrl: './edit-account.page.html',
  styleUrls: ['./edit-account.page.scss'],
})
export class EditAccountPage implements OnInit {

  id: any;
  fname: any;
  lname: any;
  contactnumber: any;
  barangay: any;
  user: any;

  constructor(
    private data: DataService,
    private router: Router,
    private loadingController: LoadingController,
    private toastController: ToastController,
    private alertController: AlertController,
    private accessProviders: AccessProviders,
    public navController: NavController,
    private platform: Platform,
    private storage: Storage,
    private activatedRoute: ActivatedRoute,
    private navParamService: NavparamService
  ) { }

  ngOnInit() {

  }

  ionViewDidEnter(){
    this.id=this.navParamService.getNavData();
    console.log(this.id);
    return new Promise(resolve =>{
      let body = {
        aksi: 'view',
        id: this.id,
        
      }
      this.accessProviders.postData(body, 'delete_product.php').subscribe((res:any) => {
        if(res.success == true) {
           console.log(res.res);
          this.user=res.res;
          this.fname=this.user.first_name;
          this.lname=this.user.lastt_name;
          this.barangay=this.user.barangay_name;
          this.contactnumber=this.user.contact_number;
        } else {
          this.presentToast('Failed'); 
        }
      });   
    });

  }
    
  async presentToast(a) {
    const toast = await this.toastController.create({
      message: a,
      duration: 1500,
      position: 'bottom'
    });
    toast.present();
  }  

}
